@use '../base' as *;

.btn {
    @include button-style($color-gradient, $color-button-shadow, $button-text-color, $button-radius);
    width: $button-width;
    height: auto; // ‚úÖ Permet d‚Äôajuster la hauteur automatiquement
    padding: 12px 24px;
    margin-bottom: 57px;
    transition: box-shadow 0.3s ease-in-out; // ‚úÖ Garde uniquement l'effet d'ombre

    @include flexbox(center, center);

    &:hover {
        background-color: darken($color-button-hover, 5%); // ‚úÖ L√©g√®rement plus fonc√© au survol
        box-shadow: 0px 8px 16px 0px $color-button-shadow; // ‚úÖ Ombre plus marqu√©e avec animation
        cursor: pointer; // ‚úÖ Change le curseur pour une meilleure UX
        animation: shadowPulse 0.3s ease-in-out;
    }

    &--explore {
        font-size: $font-size-medium;
    }
}